import React from 'react';
import _get from 'lodash.get';
import { gql, useMutation, useQuery } from '@apollo/client';
import { Formik, FormikHelpers } from 'formik';
import _isEmpty from 'lodash.isempty';
import MainCard from '../../../ui-component/cards/MainCard';
import graphqlSchema from '../../../graphql';
import Loader from '../../../ui-component/Loader';
import Profile from '../../application/user/components/Profile';
import { userValidationSchema } from '../../application/user/Validatation';
import { TEditUserProfile } from '../../../types';
import { useSnackBar } from '../../../hooks';
import { convertPhoneNumber } from '../../../helpers';

const ProfilePage = () => {
	const showSnackBar = useSnackBar();
	const { loading, data, error } = useQuery(gql(graphqlSchema.getMyProfile), {});
	const {
		loading: loadingUserGroups,
		error: userGroupsError,
		data: userGroupsData,
	} = useQuery(gql(graphqlSchema.users_UserGroups));
	const [doUpdateUserProfile] = useMutation(
		gql(graphqlSchema.updateMyProfile)
	);
	const user = _get(data, ['users_GetMyUserProfile']) || {};

	const groups = _get(userGroupsData, 'users_UserGroups');
	const firstName = _get(user, 'FirstName') || '';
	const middleName = _get(user, 'MiddleName') || '';
	const lastName = _get(user, 'LastName') || '';
	const phone = _get(user, 'Phone') || '';
	const email = _get(user, 'Email') || '';
	const userGroup = _get(user, ['UserGroups', '0', 'GroupName']);
	const isDisabled = !_get(user, 'Enabled', false);
	const title = `${firstName} ${lastName} ${isDisabled ? ' - Disabled' : ''}`;
	const dreNumber = _get(user, 'DRENumber') || '';
	const licenseType = _get(user, 'LicenseType') || '';

	const initialValues = {
		firstName,
		middleName,
		lastName,
		email,
		phone,
		userGroup,
		dreNumber,
		licenseType,
	};

	if (loading || loadingUserGroups) return <Loader />;

	if (userGroupsError || error) {
		const message =
			_get(error, 'message') ||
			_get(userGroupsError, 'message', 'Something went wrong');
		showSnackBar({
			message,
		});
		return <></>;
	}

	const onSubmit = async (
		values: TEditUserProfile,
		{ setStatus, setSubmitting }: FormikHelpers<TEditUserProfile>
	) => {
		try {
			const { firstName, lastName, phone, middleName, licenseType, dreNumber } =
				values;
			const variables = {
				FirstName: firstName,
				LastName: lastName,
				MiddleName: middleName,
				Phone: _isEmpty(phone) ? undefined : convertPhoneNumber(phone),
				LicenseType: licenseType,
				DRENumber: dreNumber,
			};
			await doUpdateUserProfile({
				variables,
			});
			showSnackBar({
				message: 'Profile has updated',
				alertSeverity: 'success',
			});
		} catch (e: any) {
			const message = _get(e, 'message', 'Something went wrong');
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({ message });
		}
	};

	return (
		<MainCard title={title} sx={{ mb: 3 }}>
			<Formik
				initialValues={initialValues}
				onSubmit={onSubmit}
				validationSchema={userValidationSchema}
			>
				{({ handleSubmit }) => (
					<form noValidate onSubmit={handleSubmit}>
						<Profile isGroupDisable groups={groups} />
					</form>
				)}
			</Formik>
		</MainCard>
	);
};

export default ProfilePage;
